@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* slick sliders */
.slick-slide {
	outline: none;
	box-sizing: border-box;

	.slick-track {
		margin: 0; } }

// arrows
.slick-arrow {
	@include size(30px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: white;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		@include size(12px);
		@include margin-horizontal(-3px);
		margin-top: -6px;
		transform: rotate(45deg);
		border: 1px solid #00519b;
		box-sizing: border-box;
		opacity: 1;
		pointer-events: none; } }

.slick-next, .slick-prev {
	&:focus,
	&:hover {
		background-color: white; }

	&.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } } }


.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-top: none;
		border-right: none;
		margin-right: 0; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-bottom: none;
		border-left: none;
		margin-left: 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.content &, & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .content & {
			padding: 4px 7px;
			margin: 0;
			background: none; }

		button {
			@include size(8px);
			padding: 0;
			border-radius: 0;
			position: relative;
			background: white;

			&::before {
				content: none; } }

		&.slick-active button {
			background-color: $base-color-alt; } } }

/* slick sliders end */
