@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* reset */
@include media('<tab-lg') {
	a, input, button,
	a:focus, input:focus, button:focus {
		-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
		-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
		outline: none !important; }
	*:focus {
		outline: none;
		-moz-outline: 3px solid white !important; }
	body {
		-webkit-font-smoothing: antialiased; }
	body, html {
		height: 100%;
		width: 100%; }
	select[disabled] {
		cursor: default; }
	form {
		margin: 0;
		padding: 0; }
	input {
		vertical-align: middle; }
	textarea, .select select, input[type="text"], input[type="email"], input[type="search"], input[type="submit"], input[type="button"], input[type="reset"], button {
		-webkit-appearance: none;
		-moz-appearance: none;
		border-radius: 0; }
	select::-ms-expand {
		display: none; }
	input[type=radio], input[type=checkbox], select {
		cursor: pointer; }
	textarea, input, button {
		//-webkit-appearance: none
		border-radius: 0; }
	input[type="radio"]:focus {
		-moz-outline-radius: 12px !important;
		-moz-outline-offset: 0 !important; }
	input[type="checkbox"]:focus {
		-moz-outline-offset: -1px !important;
		-moz-outline: 1px solid #000 !important; }
	input::-ms-clear {
		display: none; }
	blockquote, q {
		quotes: none; }
	blockquote::before, blockquote::after, q::before, q::after {
		content: none; }
	ins {
		text-decoration: none; }
	del {
		text-decoration: line-through; }
	fieldset {
		border: none;
		margin: 0;
		padding: 0; }
	table {
		border: 0;
		width: 100%;
		padding: 0;
		margin: 0; }
	ul {
		list-style: none;
		padding: 0;
		margin: 0; }
	img {
		border: none;
		vertical-align: top;
		max-width: 100%;
		height: auto;

		table & {
			max-width: none; } } }

/* reset end */
/* layout */
body, .wrapper {
	min-width: 320px; }

.wrapper {
	&, .inside & {
		@include media('<tab-lg') {
			background: $bg-color-base; } } }

html {
	&.menu-is-open {
		@include scroll-blocked(); } }

.for-mob {
	display: none; }

.bg-alt {
	@include media('<tab-lg') {
		.wrapper, .content {
			background-color: #f7f7f7; } } }

.clear-layout {
	clear: both;
	@include font(0, 0); }

.mob-show {
	display: none;

	@include media('<tab-lg') {
		display: block; } }

.mob-hide {
	@include media('<tab-lg') {
		display: none; } }

.table-auto {
	@include media('<tab-lg') {
		position: relative;
		overflow: auto;
		max-width: 100%;
		-webkit-overflow-scrolling: touch;
		margin-bottom: $bottom-space;

		table {
			margin-bottom: 0;
			width: auto;
			min-width: 100%; } } }

.wrapper select {
	@include media('<tab-lg') {
		overflow: hidden;
		display: block;
		position: relative;
		width: 100%;
		height: 40px;
		cursor: pointer;
		padding: 9px 27px 9px 14px;
		text-indent: 0;
		text-overflow: '';
		border: 1px solid $border-color;
		border-radius: 0;
		@include font(14px, 16px);
		white-space: nowrap;
		background: white;
		box-shadow: none;
		outline: none; }

	@include media('<mob-lg') {
		@include padding-horizontal(9px);
		font-size: 12px; } }

input[type="text"], input[type="tel"], input[type="email"], input[type="password"], textarea, .bx_search_container .bx_field .bx_input_text {
	@include media('<tab-lg') {
		width: 100%;
		display: block;
		border: 1px solid $border-color;
		background: #fff;
		font-family: Tahoma, sans-serif;
		font-size: 14px;
		line-height: 16px;
		height: 40px;
		color: $text-color;
		padding: 11px 19px;
		margin: 0;
		box-shadow: none;
		outline: none;
		border-radius: 0;
		box-sizing: border-box; }

	@include media('<mob-lg') {
		@include padding-horizontal(12px);
		font-size: 12px; } }

textarea {
	@include media('<tab-lg') {
		height: 118px;
		max-height: 118px;
		resize: none; } }

/* layout end */

/* header */
.header {
	@include media('<tab-lg') {
		height: $header-height--lg; }

	@include media('<mob-lg') {
		height: $header-height--sm; } }

/* header end */

/* navigation */
ul.topNav {
	@include media('<desk-sm') {
		& > li {
			&, &:last-child {
				margin-left: 5px;
				@include padding-horizontal(5px); } } }

	@include media('<tab-lg') {
		display: none; } }

.nav-mob {
	display: none;
	position: fixed;
	@include position(0);
	@include size(100%);
	overflow-x: hidden;
	overflow-y: auto;
	z-index: 99;
	background-color: $bg-color-base;
	box-sizing: border-box;

	&.menu-is-open {
		display: block; } }

.nav-mob-head {
	@include clear; }

.topNav-mob {
	$space: 23px;
	position: relative;
	background-color: $base-color;
	font-family: Tahoma, sans-serif;
	@include font(15px, 20px);

	li {
		position: relative;
		min-height: 50px;

		a {
			display: block;
			position: relative;
			color: white;
			text-decoration: none;
			padding: 15px $space;

			&:hover {
				text-decoration: none;
				color: $base-color-alt; } }

		&.has-drop > a {
			padding-right: 65px; }

		&.current {
			& > a {
				color: $text-color;
				background-color: $base-color-alt;

				&::before {
					display: none; } }

			& + li > a {
				&::before {
					display: none; } } } }

	// firs level
	& > li {
		border-top: 1px solid #3a76ae;

		&:first-child {
			border-top: none; }

		& > a {
			font-family: pf_dintext_probold, sans-serif;
			font-size: 16px;
			text-transform: uppercase; }

		& > ul {
			display: none;

			&.is-open {
				display: block; } } }

	// second and lower levels
	ul {
		background-color: shade($base-color, 85);

		a {
			padding-left: $space*2; } }

	// second level
	& > li > ul {
		a {
			&::before {
				@include pseudo();
				border-top: 1px solid #3a6c9b;
				left: $space*2;
				right: 0;
				top: 0; } }

		li:first-child {
			a {
				&::before {
					content: none; } } } } }

.topNav-angle {
	display: none;
	position: absolute;
	right: 0;
	top: 0;
	@include font(0, 0);
	@include size(50px, 50px);
	background-image: url(../../i/arr_up_white.svg); // for preload
	background-image: url(../../i/arr_dn_white.svg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: 14px 8px;
	cursor: pointer;

	&::before {
		$height: 22px;
		@include pseudo();
		height: $height;
		border: solid #3a76ae;
		border-width: 0 0 0 1px;
		top: 50%;
		margin-top: -#{round($height/2)}; }

	&.is-open {
		background-image: url(../../i/arr_up_white.svg); }

	.topNav-mob .has-drop > & {
		display: block; } }

.nav-mob-foot {
	padding: 18px 20px;

	h4 {
		font-family: pf_dintext_proregular, sans-serif;
		@include font(14px, 18px);
		color: $base-color;
		text-transform: uppercase;
		margin-bottom: 10px;
		padding: 0; } }

.cont-list {
	@include font(14px, 24px);
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; }

	& > li {
		margin-bottom: 12px;

		&:last-child {
			margin-bottom: 0; } }

	ul {
		li {
			position: relative;
			padding-left: 22px; }

		img {
			position: absolute;
			left: 0;
			top: 6px; } } }

/* navigation end */

/* logo */
.logo {
	@include media('<tab-lg') {
		left: $header-height--lg;
		top: 50%;
		transform: translate($layout-space--lg, -50%); }

	@include media('<tab-md') {
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); }

	@include media('<tab-sm') {
		left: $header-height--lg;
		transform: translate(0, -50%); }

	@include media('<mob-lg') {
		width: 153px;
		left: $header-height--sm;
		background-position: center;
		background-size: contain; } }

/* logo end */

/* login */
.enterLink {
