			background-position: center; } }

	@include media('<mob-lg') {
		@include size($header-height--sm); } }

a.userLink {
	@include media('<tab-lg') {
		position: relative;
		float: right;
		top: auto !important;
		right: auto;
		background-image: url(../../i/login.png);
		background-repeat: no-repeat;
		background-position: center;
		background-color: transparent;
		margin: 0;
		padding: 0;
		@include size($header-height--lg);
		@include font(0, 0); }

	@include media('<mob-lg') {
		@include size($header-height--sm);
 } }		//display: none !important

a.userLogout {
	@include media('<tab-lg') {
		position: relative;
		float: right;
		top: auto !important;
		right: auto;
		background-image: url(../../i/logout.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-color: transparent;
		background-size: 20px;
		@include size($header-height--lg);
		@include font(0, 0);

		img {
			display: none; } }

	@include media('<mob-lg') {
		@include size($header-height--sm); } }

a.cardLink {
	display: none;

	@include media('<tab-lg') {
		display: block;
		float: right;
		position: relative;
		top: auto !important;
		right: auto;
		background-image: url(../../i/cart.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-color: transparent;
		background-size: 20px;
		@include size($header-height--lg);
		@include font(0, 0);

		img {
