		display: none !important; } }

/* login end */

/* auth */
.auth {

	@include media('<mob-lg') {
		width: calc(100% - #{$layout-space--sm*2});
		top: $layout-space--sm;
		right: $layout-space--sm;
		box-sizing: border-box;
		padding: $layout-space--sm;
		box-shadow: 0 0 35px rgba(0, 0, 0, 0.35);

		.column {
			display: block;
			float: none;
			margin: 0 0 $bottom-space; }

		input[type="email"], input[type="password"] {
			display: block;
			box-sizing: border-box;
			width: 100%; }

		a.btn {
			margin-top: 0; }

		.meta {
			margin: 0; }

		.links {
			float: none;
			margin-bottom: $bottom-space;

			a {
				margin: 0 10px 0 0; } }

		input[type="checkbox"] {
			vertical-align: top; }

		label {
			vertical-align: top; } } }

/* auth end */

/* button menu */
.btn-menu {
	position: absolute;
	display: none;

	$btn-menu-bg: $bg-color-base;
	$btn-menu-color: $text-color;
	$btn-menu-lines-width: 22px;
	$btn-menu-lines-height: 3px;
	$btn-menu-lines-space: 4px;
	$btn-menu-lines-color: $base-color;

	$btn-menu-cross-width: $btn-menu-lines-width;
	$btn-menu-cross-height: $btn-menu-lines-height;
	$btn-menu-cross-color: $btn-menu-lines-color;

	@mixin menu-btn-line() {
		@include size($btn-menu-lines-width, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color; }

	@mixin menu-btn-cross-line() {
		@include size($btn-menu-cross-width, $btn-menu-cross-height);
		background-color: $btn-menu-cross-color; }

	text-decoration: none;
	color: $text-color;
	@include size($header-height--lg);
	background-color: $bg-color-base;

	@include media('<tab-lg') {
		display: block; }

	@include media('<mob-lg') {
		@include size($header-height--sm); }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include menu-btn-cross-line;
		@include position(50%);
		@include margin($top: -#{round($btn-menu-cross-height*0.5)}, $left: -#{round($btn-menu-cross-width*0.5)});
		opacity: 0; }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include menu-btn-line;
		@include margin($top: -#{round($btn-menu-lines-height*0.5)}, $left: -#{round($btn-menu-lines-width*0.5)});

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include menu-btn-line; }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {

		&::before, &::after {
			background-color: $base-color; }

		& > i {
			background-color: $base-color;

			&::before, &::after {
				background-color: $base-color; } } }

	// active
	&.menu-is-open {

		&::before, &::after {
			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } }

		// hover on active
		//.no-touchevents &.active:hover

		// > i

		//&::before, &::after
 } }		//	background-color: $bg-color-base

/* button menu end */

/* button close menu */
.btn-close {
	display: block;
	position: relative;
	float: left;

	$btn-menu-bg: $bg-color-base;
	$btn-menu-color: $text-color;
	$btn-menu-cross-width: 22px;
	$btn-menu-cross-height: 3px;
	$btn-menu-lines-space: 4px;
	$btn-menu-cross-color: $base-color;

	@mixin menu-btn-cross-line() {
		@include size($btn-menu-cross-width, $btn-menu-cross-height);
		background-color: $btn-menu-cross-color; }

	text-decoration: none;
	color: $text-color;
	@include size($header-height--lg);
	background-color: $bg-color-base;

	@include media('<mob-lg') {
		@include size($header-height--sm); }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include menu-btn-cross-line;
		@include position(50%);
		@include margin($top: -#{round($btn-menu-cross-height*0.5)}, $left: -#{round($btn-menu-cross-width*0.5)}); }

	&::before {
		transform: rotate(45deg) translateZ(0); }

	&::after {
		transform: rotate(-45deg) translateZ(0); } }

/* button close menu end */

/* button */
a.btn, .btn {
	@include media('<tab-lg') {
		& {
			@include display-inline-block-top;
			text-align: center;
			padding: 8px 20px;
			@include font-alt-light;
			@include font(14px, 24px);
			color: white;
			white-space: nowrap;
			text-transform: uppercase;
			border-radius: 0;
			text-shadow: none;
			box-shadow: none;
			border: none;
			box-sizing: border-box;
			cursor: pointer;
			background-color: #00a6eb; }

		&:hover {
			box-shadow: none;
			text-shadow: none;
			background-color: #003b71; }

		&:active {
			box-shadow: none;
			text-shadow: none;
			background-color: #003b71;
			color: $base-color-alt; }

		&.disabled {
			box-shadow: none;
			text-shadow: none;
			cursor: default; }

		&.callme,
		&#callme,
		&.callme-js {
			@include media('<tab-lg') {
				cursor: pointer;
				background: #4caf50;
				text-shadow: none;
				box-shadow: none;
				min-width: 240px; }

			@include media('<mob-lg') {
				min-width: 0; }

			&:hover {
				color: #fff;
				background: #1b5e20;
				text-shadow: none;
				box-shadow: none; } } } }

