/* promo */
.intro {
	height: auto;
	@include clear; }

.intro .catalogue {
	float: left;
	position: relative;
	left: auto;
	top: auto;
	box-sizing: border-box;
	min-height: 0;

	@include media('<tab-lg') {
		& ul {
			display: none; } }

	@include media('<tab-md') {
		width: 50%;
		padding: 0;

		.title {
			display: none; } }

	@include media('<mob-lg') {
		float: none;
		width: auto; }

	.catalogue-mob {
		display: none;
		font: 15px/20px Tahoma, sans-serif;

		& > div {
			position: relative;

			a {
				color: #fff;
				text-decoration: none;
				display: block;
				padding: 8px 30px;

				&:hover {
					color: #ffbf14;
					text-decoration: underline; } } }

		@include media('<tab-lg') {
			display: block; }

		@include media('<tab-md') {
			height: 100%;
			width: 100%;
			display: table;

			& > div {
				display: table-row;
				padding: 0;

				a {
