		margin: -#{$layout-space-v--lg} -#{$layout-space--lg} $layout-space-v--lg;
		padding: #{round($layout-space--lg*0.52)} #{$layout-space--lg};
		background-color: $base-color;
		@include font(12px, 18px);
		color: white;
		letter-spacing: normal;
		white-space: nowrap;
		box-sizing: border-box;
		z-index: 4;
		user-select: none;

		&::after {
			@include pseudo();
			right: $layout-space--lg;
			top: 50%;
			@include triangle(bottom, $base-color-alt, 5px, 1.2);
			transform: translate(0, -50%); }

		&.open::after {
			transform: rotate(-0.5turn) translate(0, 50%); } }

	@include media('<mob-lg') {
		margin: -#{$layout-space-v--sm} -#{$layout-space--sm} $layout-space-v--sm;
		padding: #{round($layout-space-v--lg*0.52)} #{$layout-space-v--sm};

		&::after {
			right: $layout-space--sm; } }

	.toggle {
		overflow: hidden;
		text-indent: -9999px;

		@include media('<tab-lg') {
			position: static;
			display: inline;
			float: none;
			@include size(auto);
			margin: 0;
			background: none;
			color: $base-color-alt;
			text-transform: uppercase;
			@include font-alt-light;
			@include font(14px, 18px);
			text-indent: 0;

			&::before {
				@include pseudo();
				left: 0;
				top: 0;
				right: 0;
				bottom: 0; } } }

	.currentPart {
		@include media('<tab-lg') {
			display: inline;
			float: none;
			@include size(auto);
			padding: 0;
			margin: 0;
			background: none;
			@include font(12px, 18px);
			color: white; } } }

.currentPart {
	&:empty {
		display: none; } }

/* narrowScreenNav end */

/* fTabs / cartTabs */
ul.fTabs, ul.cartTabs {
	@include clear-spacing;
	line-height: 0;
	padding-bottom: 1px;

	@include media('<tab-lg') {
		//margin-bottom: -1px
		padding: 0 0 1px 1px; }

	li {
		background-color: $bg-color-base; }

	li, li.ui-tabs-active {
		box-sizing: border-box;
		@include font(12px, 16px);
		letter-spacing: normal;
		@include display-inline-block-top;
		float: none;
		padding: 0;
		border: 1px solid $base-color-alt;
		//box-shadow: 1px 0 0 $base-color-alt
		//border-right: none
		margin: 0 0 -1px -1px;

		a {
			display: block;
			padding: 12px 10px;
			border: none; }

		&:last-child {
			border-right: 1px solid $base-color-alt;
			box-shadow: none; }

		&:first-child {
			border: 1px solid $base-color-alt;
 }			//border-right: none

		@include media('<mob-lg') {
			width: 50%; } } }

ul.cartTabs {
	li, li.ui-tabs-active {
		@include media('<mob-lg') {
			width: 100%; } } }

/* fTabs / cartTabs end */

/* filterBlock */
.filterBlock {
	@include media('<tab-lg') {
		padding: $grid-space--lg;
		border: none; }

	.fParamsSizes {
		@include media('<tab-lg') {
			float: none;
			width: auto; } }

	.fParams, .fParams2, .fastSearch {
		@include media('<tab-lg') {
			@include margin-horizontal(-#{$grid-space--lg});
			margin-bottom: $grid-space--lg;
			border-bottom: 1px solid #e6ad12;
			box-sizing: border-box; } }

	.fParams, .fParams2 {
