 } }		//display: inline-block

.paging-mob {
	display: none;
	border: 1px solid $border-color;
	text-align: center;
	height: 40px;
	position: relative;
	box-sizing: border-box;

	&, .content & {
		list-style: none;
		padding: 0;
		margin: 0;

		li {
			padding: 0;
			margin: 0;
			background: none; } }

	li {
		font-size: 14px;
		line-height: 38px;
		box-sizing: border-box;

		a {
			display: block;
			text-decoration: none;
			box-sizing: border-box; }

		&.first_, &.prev_, &.next_, &.last_ {
			position: absolute;
			top: -1px;
			border: 1px solid $border-color;
			background-repeat: no-repeat;
			background-position: center;
			background-size: 14px 14px;

			a {
				@include size(38px);
				text-indent: -9999px;
				overflow: hidden;
				cursor: pointer; }

			&.disabled {
				a {
					cursor: default; } } }

		&.first_ {
			left: -1px;
			margin-right: -1px;
			background-image: url(../../i/arr_left_dbl.svg);

			&.disabled {
				background-image: url(../../i/arr_left_dbl_dis.svg); } }

		&.prev_ {
			left: 38px;
			margin-right: -1px;
			background-image: url(../../i/arr_left.svg);

			&.disabled {
				background-image: url(../../i/arr_left_dis.svg); } }

		&.next_ {
			right: 38px;
			margin-left: -1px;
			background-image: url(../../i/arr_right.svg);

			&.disabled {
				background-image: url(../../i/arr_right_dis.svg); } }

		&.last_ {
			right: -1px;
			margin-left: -1px;
			background-image: url(../../i/arr_right_dbl.svg);

			&.disabled {
				background-image: url(../../i/arr_right_dbl_dis.svg); } }

		.counter_ {
			@include display-inline-block-top; } }

	@include media('<tab-lg') {
		display: block; }

	@include media('<mob-lg') {
		.counter_ {
			span {
				display: none; } } } }

.finedCount {
	position: relative;
	display: block;
	float: none;
	clear: both;
	right: auto;
	padding: 0;
	margin-bottom: $bottom-space--sm;

	@include media('<tab-lg') {
		font-size: 12px;
		margin-bottom: $bottom-space; } }

