/* table-resp */
.table-resp {
	@include media('<tab-lg') {
		display: block;
		@include clear-spacing;

		&, .content & {
			width: auto !important;
			@include margin-horizontal(-#{round($grid-space--lg/2)}); }

		tbody, tfoot, thead, tr {
			display: block;
			position: relative;
			box-sizing: border-box; }

		tr {
			@include display-inline-block-top;
			width: 50%;
			margin-bottom: $bottom-space;
			@include padding-horizontal(#{round($grid-space--lg/2)});

			&:nth-child(2n+1) td {
				background-color: white; }

			&.tresp-head {
				display: none; } }

		td {
			display: block;
			box-sizing: border-box;
			text-align: left;
			border: solid $border-color;
			border-width: 0 1px 1px;
			background-color: white;
			@include clear-spacing;

			&:first-child {
				border-top-width: 1px; }

			&::before {
				@include display-inline-block-middle;
				content: attr(data-label);
