					margin: 0;

					img {
						margin: 0; } } }

			&.remainder {
				&::before {
					//display: block
					//width: 100%
					padding-right: 5px;
					margin-bottom: 10px;
					width: auto; }

				.tresp-val {
					display: block;
					width: auto; } }

			.orderCount {
