			&, &.tresp-del a, &.tresp-number span {
				padding: #{round($grid-space--lg*0.5)} $grid-space--lg;
				box-sizing: border-box; }

			&.tresp-number span, &.tresp-del a {
				display: block;
				@include padding-horizontal(6px);
				min-width: 38px;
				min-height: 38px; } } }

	@include media('<tab-md') {
		.basket & tr {
			padding-bottom: 39px; }

		tr {
			display: block;
			width: auto; }

		td {
			display: block;

			&.tresp-name {
				.tresp-val {
					display: block;
					width: auto; } }

			&.tresp-count {
				position: absolute;
				bottom: 0;
				left: #{round($grid-space--lg/2)};
				right: #{round($grid-space--lg/2)};
				padding: 0;
				border: none;
				@include size(auto, 40px);

				.tresp-val {
					height: 100%;
					display: block;
					width: auto; }

				&::before {
					display: none; } }

			.counter {
				width: auto;
				max-width: none; } } }

